<template>
  <div>
    <div class="header">
      <!-- <h3>共享费用用年度报销账单</h3> -->
      <div class="content">
        <div id="app">
          <div id="myChart3" style="width:100%; height:300px;"></div>
        </div>
        <div class="content_lis">
          <ul>
            <li>
              <strong>个人借款报销</strong>
            </li>
            <li>瞧瞧咱们今年还有哪些开销</li>
            <li>
              今年您一共发起XX笔借款单，借款金额总计
              <em>xxx</em>元
            </li>
            <li>
              其中已冲销还款
              <em>22</em>元
            </li>
            <li>
              还有借款余额
              <em>888</em>元哦！
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      myChart2: ""
    };
  },
  mounted() {
    this.drawChart3();
  },
  methods: {
    drawChart3() {
      let myChart3 = this.$echarts.init(document.getElementById("myChart3"));
      window.onresize = myChart3.resize;
      let option = {
        color: ["#5b9bd5", "#ed7d31", "#a5a5a5"],
        grid: {
          x: 0,
          y: 20
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          top: "1n5%",
          x: 'left',
          y: "10",
          itemWidth: 20,
          data: ["直接访问", "邮件营销", "联盟广告"]
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["40%", "60%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              { value: 335, name: "直接访问" },
              { value: 310, name: "邮件营销" },
              { value: 234, name: "联盟广告" }
            ]
          }
        ]
      };
      myChart3.setOption(option);
    }
  }
};
</script>

<style scoped>
#myChart3 {
  background-image: linear-gradient(#fefefe, #d4d4d4);
  /* background-image: linear-gradient(to bottom right, #dcdcdc, #e9e9e9); */
}
.header {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding-top: 320px;
}
.header h3 {
  font-size: 18px;
  text-align: center;
}
.content_lis {
  padding: 6px 0 0 6px;
  background: #c6e0b4;
}

.content_lis li {
  font-size: 14px;
}
</style>


