<template>
  <div>
    <div class="header">
      <!-- <h3>共享费用用年度报销账单</h3> -->
      <div class="content">
        <div id="app">
          <div id="myChart4" style="width:100%; height:300px;"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      myChart2: ""
    };
  },
  mounted() {
    this.drawChart4();
  },
  methods: {
    drawChart4() {
      let myChart4 = this.$echarts.init(document.getElementById("myChart4"));
      window.onresize = myChart4.resize;
      let option = {
        color:['#a5a5a5','#ed7d31','#5b9bd5'],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // type: 'shadow'
          }
        },
        legend: {
          data: ["2011年", "2012年","2013年"],
          bottom: "bottom"
        },
        grid: {
          y:'30%',
          left: "3%",
          right: "4%",
          bottom: "12%",
          containLabel: true
        },
        xAxis: {
          // type: 'value',
          // boundaryGap: [0, 0.01]
        },
        yAxis: {
          type: "category"
          // data: ['巴西','印尼','美国','印度','中国','世界人口(万)']
        },
        series: [
          {
            name: "2011年",
            type: "bar",
            data: [18203],
          },
          {
            name: "2012年",
            type: "bar",
            data: [19325]
          },
          {
            name: "2013年",
            type: "bar",
            data: [19325],
            barGap: '30%',
            barCategoryGap:'30%',//柱图间距
          }
        ]
      };
      myChart4.setOption(option);
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding-top: 320px;
}
.header h3 {
  font-size: 18px;
  text-align: center;
}
.content_lis {
  padding: 0 6px;
  background: #c6e0b4;
}

.content_lis li {
  font-size: 14px;
}
</style>


