<template>
    <div class="box">
      <!-- <van-nav-bar left-arrow title="标题"></van-nav-bar> -->
      <div class="header">
        <van-panel>
          <div class="content_bar">
            <h3 class="tit">共享费用用年度报销账单</h3>
            <div class="content_lis">
              <ul>
                <li>
                  <strong>全年费用报销总量</strong>
                </li>
                <li>
                  您一共发起
                  <em>666</em> 笔报销单
                </li>
                <li>
                  报销总金额：
                  <em>22222</em>
                </li>
                <li>
                  <strong>
                    其中最快报销到账仅花了
                    <em>6688</em>天时间！
                  </strong>
                </li>
              </ul>
            </div>
          </div>
        </van-panel>
        <div id="app">
          <div id="myChart" style="width:100%; height:260px;"></div>
        </div>
        <div class="content_item">
          <h3>厉害了！一看您就是发单能手,发单达人非你莫属！</h3>
        </div>
      </div>
    </div>
    <!-- <div class="box">
      <Chart2 />
    </div>-->
    <!-- <div class="box">
      <Chart3 />
    </div>-->
    <!-- <Chart5 /> -->
</template>

<script>
import Chart2 from "../Chart/Chart2";
import Chart3 from "../Chart/Chart3";
import Chart5 from "../Chart/Chart5";
export default {
  name: "app",
  components: {
    Chart2,
    Chart3,
    Chart5
  },
  data() {
    return {
      myChart: ""
    };
  },
  mounted() {
    this.drawChart();
  },
  methods: {
    drawChart() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
       window.onresize = myChart.resize;
      
      // 指定图表的配置项和数据
      let option = {
        color: [
          "#a5a5a5",
          "#ffc000",
          "#5981ca",
          "#70ad47",
          "#5b9bd5",
          "#ed7d31"
        ],
        title: {
          x: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          // orient: "vertical",
          bottom: "5%",
          data: ["直接访问", "邮件营销", "联盟广告","视频广告","搜索引擎"]
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "55%",
            center: ["46%", "40%"],
            data: [
              { value: 335, name: "直接访问" },
              { value: 310, name: "邮件营销" },
              { value: 234, name: "联盟广告" },
              { value: 135, name: "视频广告" },
              { value: 1548, name: "搜索引擎" }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      // 使用刚指定的配置项和数据显示图表。
      myChart.setOption(option);
    }
  }
};
</script>

<style scoped>
.van-cell {
  padding: 0;
}
.header .tit {
  font-size: 20px;
  text-align: center;
}
.header h3 {
  font-size: 14px;
  padding: 0 6px;
}
.content_bar {
  /* padding: 2px 0; */
}
.content_bar .content_lis {
  background: #c6e0b4;
}
.content_bar .content_lis ul {
  padding: 4px 6px 0 4px;
}
.content_bar em {
  color: #ff0000;
  font-weight: 30;
}
.content_bar li {
  font-size: 14px;
  height: 24px;
  line-height: 18px;
}
.content_item {
  height: 54px;
  line-height: 54px;
}
.content_item,
.content_bar {
  background: #bdd7ee;
}
</style>


