<template>
  <div>
    <div class="header">
      <!-- <h3>共享费用用年度报销账单</h3> -->
      <van-panel>
        <div class="content_lis">
          <ul>
            <li>
              <strong>说了这么多，发单达人有没有被退单过呢？</strong>
            </li>
            <li>
              <strong>让我们来看看！</strong>
            </li>
            <li>
              【关键退回】
              <em>6</em> 次
            </li>
            <li>
              【非关键退回】
              <em>6</em> 次
            </li>
          </ul>
        </div>
      </van-panel>
      <div id="app">
        <div id="myChart5" style="width:100%; height:300px;"></div>
      </div>
      <div class="footer_box">
        <ul class="f_item">
          <li>棒棒哒！你的准确率已超过90%的小伙伴！</li>
          <li>争取实现百分百发单准确，一发即过！</li>
        </ul>
        <div class="f_content">
          <ul>
            <li>2019年的个人重要事件别忘了</li>
            <li>
              咱们还有
              <em>福利费-结婚礼金</em>报销
            </li>
            <li>祝福您 新婚快乐，生活甜甜蜜蜜哦！</li>
          </ul>
          <ul>
            <li>2019年的个人重要事件别忘了</li>
            <li>
              咱们还有
              <em>福利费-结婚礼金</em>报销
            </li>
            <li>祝福您 新婚快乐，生活甜甜蜜蜜哦！</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      myChart2: ""
    };
  },
  mounted() {
    this.drawChart5();
  },
  methods: {
    drawChart5() {
      let myChart5 = this.$echarts.init(document.getElementById("myChart5"));
      window.onresize = myChart5.resize;
      let option = {
        color: ["#5b9bd5","#ed7d31","#a5a5a5","#ffc000"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
         legend: {
          data: ["2011年", "2012年","2013年"],
          bottom: "bottom"
        },
        grid: {
          y: 35,
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: ["Mon", "Tue", "Wed", "Thu"],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "直接访问",
            type: "bar",
            barWidth: "90%",
            data: [10, 52, 200, 334],
            barCategoryGap: "100%" //柱图间距
          }
        ]
      };
      myChart5.setOption(option);
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding-top: 280px;
}
.header h3 {
  font-size: 16px;
  text-align: center;
}
.footer_box {
  width: 100%;
  height: 19px;
}
.f_content ul:nth-child(1) {
  margin-bottom: 24px;
}
.f_content ul {
  padding: 0 6px;
}
.content_lis {
  background: #c6e0b4;
}
.content_lis ul{
  padding: 6px 0 0 6px;
}
.content_lis li {
  font-size: 14px;
  height: 24px;
  line-height: 18px;
}
.footer_box .f_item {
  /* width: 100%; */
  margin-bottom: 16px;
  background: #bdd7ee;
  padding: 0 6px;
}
.content_lis li em {
  color: #ff0000;
}

.footer_box li {
  font-size: 14px;
  height: 24px;
  line-height: 24px;
}

.footer_box .f_content {
  background: #c6e0b4;
}
</style> 


