<template>
  <div>
    <div class="header">
      <!-- <h3>共享费用用年度报销账单</h3> -->
      <div class="content_bar">
        <div class="content_lis">
          <ul>
            <li>
              <strong>差旅费报销</strong>
            </li>
            <li>
              您今年累计出差
              <em>666</em> 次
            </li>
            <li>
              足迹遍布
              <em>广州，上海</em>
            </li>
            <li>千山万水，挡不住前进的步伐！</li>
          </ul>
        </div>
      </div>

      <div id="app">
        <div id="myChart2" style="width:100%; height:220px;"></div>
      </div>
      <div class="content_item">
        <h3>
          出行
          <em>飞机</em>为主，
          <em>空中飞人</em>就是你~！
        </h3>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      myChart2: ""
    };
  },
  mounted() {
    this.drawChart2();
  },
  methods: {
    drawChart2() {
      let myChart2 = this.$echarts.init(document.getElementById("myChart2"));
      window.onresize = myChart2.resize;
      // window.addEventListener("resize", () => {
      //   this.myChart2.resize();
      // });
      let option = {
        color: ["#5b9bd5"],
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          splitLine: {
            show: false
          }
        },
        yAxis: {
          show: "false",
          type: "value",
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        },
        grid: {
          x: 50,
          y: 35
        },
        series: [
          {
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: "line",
            normal: {
              show: false
            }
          }
        ]
      };
      myChart2.setOption(option);
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.header h3 {
  font-size: 14px;
  text-align: center;
}
.content_bar {
  /* padding: 24px 0; */
}
.content_bar .content_lis {
  background: #c6e0b4;
}
.content_bar .content_lis li {
  height: 24px;
  line-height: 24px;
  margin: 0 6px;
}

.content_bar em {
  color: #ff0000;
  font-weight: 30;
}
.content_item em {
  color: #ff0000;
}
.content_bar li {
  font-size: 14px;
  height: 24px;
  line-height: 18px;
}
</style>


